.loading-wrapper {
    padding: 10px;
    width: 100%;
    text-align: left;
    margin: 20px 0px;
    -webkit-box-shadow: 0 2px 10px rgba(0, 0, 0, .050);
            box-shadow: 0 2px 10px rgba(0, 0, 0, .050);
    border: 1px solid whitesmoke
}

.loading {
    height: 100px;
    width: 70%;
    display: inline-block;
    background: -webkit-linear-gradient(165deg, whitesmoke, #eee, whitesmoke, #eee);
    background: -o-linear-gradient(165deg, whitesmoke, #eee, whitesmoke, #eee);
    background: linear-gradient(-75deg, whitesmoke, #eee, whitesmoke, #eee);
    background-size: 400% 400%;
    -webkit-animation: Gradient 1.5s ease infinite forwards;
            animation: Gradient 1.5s ease infinite forwards;
    position: relative;
    overflow: hidden;
}

span {
    position: absolute;
    background: white
}

#span-1 {
    width: 10px;
    height: 100%;
    left: 100px
}

#span-5 {
    top: 0px;
    width: 100%;
    height: 20px;
    left: 100px
}

#span-2 {
    top: 50%;
    -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
            transform: translateY(-50%);
    width: 100%;
    height: 20px;
    left: 100px
}

#span-3 {
    top: 80px;
    width: 80%;
    height: 100px;
    left: 100px
}

#span-4 {
    bottom: 0;
    right: 0;
    width: 100px;
    height: 60px;
    /* background: red */
}

@-webkit-keyframes Gradient {
    0% {
        background-position: 100% 50%
    }

    100% {
        background-position: 0% 50%;
    }
}

@keyframes Gradient {
    0% {
        background-position: 100% 50%
    }

    100% {
        background-position: 0% 50%;
    }
}


