.form-wrapper {
    padding: 10px;
    position: relative;
    display: table;
    clear: both;
    width: 768px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box
}

.form-wrapper input {
    border-style: none;
    padding: 10px 20px;
    border: 1px solid #eee;
    float: left;
    width: 23%;
    margin-right: 10px;
    -webkit-transition: 300ms ease;
    -o-transition: 300ms ease;
    transition: 300ms ease
}

.form-wrapper input:focus {
    border-color: #00bfa5
}

.form-wrapper button {
    float: right;
    width: 10%
}


