.item-wrapper {
    padding: 10px;
    width: 100%;
    text-align: left;
    margin: 20px 0px;
    -webkit-box-shadow: 0 2px 10px rgba(0, 0, 0, .050);
            box-shadow: 0 2px 10px rgba(0, 0, 0, .050);
    border: 1px solid whitesmoke
}

.item {
    width: 100%;
    height: 100px;
}

.item figure {
    margin: 0;
    padding: 0;
    height: 100px;
    width: 100px;
    overflow: hidden;
    display: inline-block;
    vertical-align: middle
}

.item .info-wrapper {
    display: inline-block;
    vertical-align: middle;
    margin-left: 10px
}

.item .info-wrapper label {
    font-weight: 600;
    font-size: 1.2rem;
    margin: 0;
    display: block;
}

.item .info-wrapper small {
    margin-bottom: 10px;
    display: inline-block;
}

p {
    margin: 0;
    color: #777;
    font-style: italic
}

.item img {
    width: 100px;
}


