.App {
  width: 768px;
  margin: 0px auto
}

.banner {
  width: 100%;
  margin: 0
}

.banner img {
  width: 100%
}

.btn {
  border-style: none;
  border: none;
  padding: 10px 20px;
  border-radius: 3px;
  -webkit-box-shadow: 0 2px 4px rgba(0, 0, 0, .2);
          box-shadow: 0 2px 4px rgba(0, 0, 0, .2);
  cursor: pointer;
  position: relative;
  -webkit-transition: 300ms ease;
  -o-transition: 300ms ease;
  transition: 300ms ease;
}

.btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: black;
  opacity: 0;
  -webkit-transition: 300ms ease;
  -o-transition: 300ms ease;
  transition: 300ms ease;
  z-index: 1001;
  overflow: hidden;
}

.btn:hover {
  -webkit-box-shadow: 0 1px 2px rgba(0, 0, 0, .2);
          box-shadow: 0 1px 2px rgba(0, 0, 0, .2);
}

.btn:hover::before {
  opacity: .1;
}

.btn-add {
  background: #00bfa5;
  color: white
}

.btn-save {
  background: #c52062;
  color: white
}


